@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
*{box-sizing:border-box}html{font-family:"Roboto", sans-serif}body{margin:0;background-color:#f0eeef}.app{max-width:1060px;margin:0 auto;padding:0 30px}.app__title{margin:25px 0;text-align:center;font-size:40px}.app__info{margin-bottom:25px}.app__text{margin-bottom:10px}.app__text:last-child{margin-bottom:0}

.todo{display:grid;grid-template-columns:2fr 5fr 2fr;grid-gap:100px;gap:100px;padding:30px 50px;background-color:#f0eeef;border:1px solid rgba(255,255,255,0.2);border-radius:10px;box-shadow:-6px -6px 16px rgba(255,255,255,0.5),6px 6px 16px rgba(209,205,199,0.5);transition:transform 0.3s, box-shadow 0.3s, border-radius 0.3s}.todo:hover{box-shadow:-16px -16px 36px 10px rgba(255,255,255,0.3),16px 16px 36px 10px rgba(209,205,199,0.3);border-radius:15px;transform:scale(1.02)}@media (max-width: 700px){.todo{grid-template-columns:1fr;grid-gap:20px;gap:20px}}.todo__title{margin-bottom:15px;line-height:19px}@media (max-width: 700px){.todo__title{margin-bottom:5px}}.todo__info{font-weight:500;font-size:20px;line-height:24px;position:relative}.todo__icon{background:url(../../static/media/completed.2025d9fb.svg) no-repeat center;width:20px;height:20px;position:absolute;left:-35px}.todo__icon--not-completed{background:url(../../static/media/not_completed.749645a0.svg) no-repeat center}

.todo__list{display:flex;flex-direction:column;grid-gap:20px;gap:20px}

